import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AddmovieService } from 'app/addmovie.service';
import { MovieModel } from 'app/admin-home/movie.model';

@Component({
  selector: 'app-movie-list',
  templateUrl: './movie-list.component.html',
  styleUrls: ['./movie-list.component.css']
})
export class MovieListComponent implements OnInit {
  movies: MovieModel[];
  constructor(private activeRoute: ActivatedRoute, private movieService: AddmovieService) { }

  ngOnInit(): void {
   var genreName = this.activeRoute.snapshot.paramMap.get('genreName');
   this.movieService.getMovies(genreName)
   .subscribe(result => {
     this.movies = JSON.parse(JSON.stringify(result));
     console.log(this.movies);
   });
   
  }

}
