import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AddmovieService } from 'app/addmovie.service';
import { MovieModel } from './movie.model';
import { HttpClient } from '@angular/common/http'
import { FileUploadService } from 'app/file-upload.service';

@Component({
  selector: 'app-admin-home',
  templateUrl: './admin-home.component.html',
  styleUrls: ['./admin-home.component.css']
})
export class AdminHomeComponent implements OnInit {

  selectedFile : File;
  shortLink: string = "";
    loading: boolean = false; // Flag variable
    file: File = null; 

  constructor(private addmovie: AddmovieService, private router: Router,private httpClient: HttpClient, private fileUploadService: FileUploadService) { }
  movie = new MovieModel(null,null,null,null,null,null,null,null,null, null);
  ngOnInit(): void {
  }
    onChange(event) {
      this.file = event.target.files[0];
  }


  onUpload() {
    this.loading = !this.loading;
    console.log(this.file);
    
}
  
AddMovie(){
  // this.httpClient.post('http://localhost:4200/assets/img/',this.selectedFile).subscribe(data=>{
  //   console.log(data);
  // })
  this.fileUploadService.upload(this.file).subscribe(
    (event: any) => {
      console.log(event);
        if (typeof (event) === 'object') {

            // Short link via api response
            this.shortLink = event.link;
            this.movie.imageUrl = this.shortLink;
            this.loading = false; // Flag variable 
            this.addmovie.newMovie(this.movie);
            alert("Success");
        }
    }
);
 
 // this.router.navigate(['/']);
  
}
onFileSelected(event){
  this.selectedFile = event.target.files[0];


}
}
