import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AddmovieService } from 'app/addmovie.service';
import { MovieModel } from 'app/admin-home/movie.model';


@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.css']
})
export class DetailComponent implements OnInit {
  focus: any;
  focus1: any;
  movies: MovieModel[];
  constructor(private activeRoute: ActivatedRoute, private movieService: AddmovieService) { }

  ngOnInit(): void {
    var movieid = this.activeRoute.snapshot.paramMap.get('id');
    
   this.movieService.findMovies(movieid)
   .subscribe(result => {
     this.movies = JSON.parse(JSON.stringify(result[0]));
    console.log(this.movies);

  })

}
}
